import { Card, Descriptions } from 'antd';

export default function Network({ chainId, network }) {
  const networkInfos = [
    {
      key: 'Network',
      label: 'Network',
      children: network,
    },
    {
      key: 'ChainId',
      label: 'ChainId',
      children: chainId,
    },
  ];

  return (
    <Card title="">
      <Descriptions items={networkInfos} />
    </Card>
  );
}
