import {
  Alert,
  Button, Card, Col, Row, Space, message,
} from 'antd';
import { useState } from 'react';

function SignMessageItem({ title, account, msg }) {
  const [ret, setRet] = useState('');
  const handleSign = async () => {
    try {
      await okxwallet.tronWeb.trx.signMessage(msg).then(setRet);
    } catch (error) {
      message.error(error.message);
    }
  };

  return (
    <Col span={6}>
      <Card direction="vertical">
        <Space direction="vertical" style={{ width: '100%' }}>
          <Button disabled={!account} block onClick={handleSign}>{title}</Button>
          <Alert
            type="info"
            message="Result"
            description={ret}
          />
        </Space>
      </Card>
    </Col>
  );
}

function SignMessage({ account }) {
  return (
    <Card title=" (signMessage)">
      <Space direction="vertical" style={{ width: '100%' }}>
        <Row gutter={6}>
          <SignMessageItem title="" account={account} msg="hello world" />
          <SignMessageItem title="" account={account} msg="" />
          <SignMessageItem title="" account={account} msg="" />
          <SignMessageItem title="" account={account} msg="" />
        </Row>
      </Space>
    </Card>
  );
}

export default SignMessage;
