import {
  Button, Card, Space,
} from 'antd';

export default function Connect({ handleConnect, account }) {
  const handleDisConnect = () => {
    okxwallet.disconnect();
  };

  return (
    <Card title="">
      <Space>
        <Button disabled={!!account} onClick={handleConnect}></Button>
        <Button disabled={!account} onClick={handleDisConnect}></Button>
      </Space>
    </Card>
  );
}
